import React from 'react'
import {createFragmentContainer} from 'react-relay'
import graphql from 'babel-plugin-relay/macro'
import CustomList from 'Components/CustomList'
import makeDate from 'Utils/MakeDate'
import Button from 'Components/LoadingButton'
import EditorJS from 'Components/Editing/Editor'
import Modal from 'Components/Modal'
import Edit from '@material-ui/icons/Edit'
import CommitRelay from 'Components/CommitRelay'
import { snackbarSuccess } from 'Store/actions/snackbar'
import RegisteredUsers from './RegisteredUsers'

function makeData(props){
  return [
    {
      id: 1,
      primaryText: "Title",
      secondaryText: props.title
    },
    {
      id: 3,
      primaryText: "Event duration",
      secondaryText: makeDate(props.startTime)+" ---- "+makeDate(props.endTime)
    },
    {
      id: 4,
      primaryText: "Max Players",
      secondaryText: props.maxPlayers
    }
  ]
}

const updateMegaDescription = graphql`
  mutation BasicDetailsMutationQuery($data: UpdateMegaDescriptionInput!) {
    updateMegaDescription(data: $data) {
      megladron {
        _id
        description
      }
      message
    }
  }
`

function BasicDetails(props) {
  let items = makeData(props.megladron)
  let [modalOpen, setModalOpen] = React.useState(false)
  let [updating, setUpdating] = React.useState(false)
  let editorRef = React.useRef()

  let description = {}
  try {
    description = JSON.parse(props.megladron.description)
  }catch(err) {
    console.log(props.megladron.description)
    console.error(err)
  }

  async function saveEditorData() {
    if(!window.confirm("Do you wish to update")) {
      return
    }
    let data = await editorRef.current.instance.save()
    console.log(data)
    try {
      data = JSON.stringify(data)
    }catch(err) {
      console.log("Error while stringifying", err)
    }
    setUpdating(true)
    CommitRelay({data: {description: data, id: props.megladron._id}}, updateMegaDescription, (response, error) => {
      setUpdating(false)
      if(!error) {
        snackbarSuccess(response.updateMegaDescription.message)
      }
    })
  }

  return (<>
    <Button isLoading={updating} onClick={() => setModalOpen(true)}><Edit />Update Know more</Button>
    <CustomList items={items} divider heading="Megladron basic details"/>  
    <RegisteredUsers id={props.megladron._id}/>
    <Modal open={modalOpen} handleClose={() => setModalOpen(false)} hideSubmit={true} title={"Update event details"} fullScreen>
      <EditorJS inputRef={editorRef} data={description}/>
      <Button isLoading={updating} onClick={saveEditorData}>Update Content</Button>
    </Modal>
  </>)
}

export default createFragmentContainer(BasicDetails, {
  megladron: graphql`
    fragment BasicDetails_megladron on Megladron {
      _id
      title
      description
      startTime
      endTime
      maxPlayers
    }
  `
})