import React from 'react'
import graphql from 'babel-plugin-relay/macro'
import {createRefetchContainer} from 'react-relay'
import Table from 'Components/Table'
import { TablePagination } from '@material-ui/core'
import Loading from 'Components/Loading'
import {makeStyles} from '@material-ui/core/styles'
import {Link} from 'react-router-dom'

const styles = makeStyles(theme => ({
  div: {
    position: "relative",
    overflow: "auto"
  },
  loadingDiv: {
    height: "100%", 
    width: "100%", 
    zindex: 1, 
    background: "rgba(0, 0, 0, 0.6)", 
    display: "flex", 
    position: "absolute", 
    alignItems: "center", 
    justifyContent: "center"
  },
}))


function RegisteredUsersRefetch(props) {

  // console.log(props)

  let [data, setData] = React.useState({page: 0, limit: 4})
  let [fetching, setFetching] = React.useState(false)
  const classes = styles()

  let teams = [] 
  try {
    teams = props.users.getRegisteredUsers.registeredUsers
  }catch(err) {
    teams = []
  }

  // if(teams.length === 0) {
  //   return <div>No data returned</div>
  // }

  
  let users = []
  teams.forEach(team => {
    team.players.forEach(player => {
      users.push([
        team.teamName, 
        team.group, 
        player.gamingUsername, 
        player.characterId, 
        <Link to={`/management/user/view/${player.user.username}`}>{player.user.username}</Link>, 
        player.user.walletBalance, 
        player.user.bonus])
    })
  })

  function refetchData({limit, page}) {
    // console.log(limit, page+1)
    setData({limit, page: page})
    console.log(limit, page)
    setFetching(true)
    props.relay.refetch({limit, page: page + 1, id: props.id}, null, err => {
      setFetching(false)
      console.log(err)
    })
  }

  // console.log(data)
  return <div className={classes.div}>
    <div className={classes.div}>
      {fetching && <div className={classes.loadingDiv}><Loading isLoading={true}/></div>}
      <Table heading={["Team Name", "Team Code", "Gaming Username", "Character Id","username", "Wallet", "Bonus",]} body={users}
        caption={"Registered Users"}
      />
    </div>
    <TablePagination 
      rowsPerPageOptions={[4, 6, 8]}
      component="div"
      count={props.users.getRegisteredUsers.totalUsers}
      rowsPerPage={data.limit}
      page={data.page}
      onChangePage={(e, page) => refetchData({limit: data.limit, page})}
      onChangeRowsPerPage={e => refetchData({limit: e.target.value, page: data.page})}
    />
  </div>
}

export default createRefetchContainer(RegisteredUsersRefetch, {
  users: graphql`
    fragment RegisteredUsersRefetch_users on Query
    @argumentDefinitions(
      page: {type: "Int", defaultValue: 1}
      limit: {type: "Int", defaultValue: 4}
      id: {type: "ID!"}
    ){
      getRegisteredUsers(id: $id, pagination: {page: $page, limit: $limit}) {
        registeredUsers {
          teamName
          group
          players {
            user {
              username
              walletBalance
              bonus
            }
            gamingUsername
            characterId
          }
        }
        page 
        totalUsers
      }
    }
  `
},
  graphql`
    query RegisteredUsersRefetchQuery ($id: ID!, $page: Int, $limit: Int){
      ...RegisteredUsersRefetch_users @arguments(id: $id, page: $page, limit: $limit)
    }
  `
)