import React from 'react'
import QueryRenderer from 'Components/QueryRenderer'
import graphql from 'babel-plugin-relay/macro'
import { Alert, AlertTitle } from '@material-ui/lab'
import Div from 'Components/Styled/FormRegular'


const query = graphql`
  query ViewAnnouncementsQuery($date: String!) {
    getAnnouncement(date: $date) {
      title
      message
      severity
    }
  }
`

function ViewAnnouncements(props) {
  return <QueryRenderer query={query} variables={{date: Date.now().toString()}} component={data => <ViewAnnouncementsComponent {...data} />}/>
}

function ViewAnnouncementsComponent(props) {
  // console.log(props)
  return <Div>
    {props.getAnnouncement.map((item, index) => <Alert severity={item.severity || "success"} key={index}>
        <AlertTitle> {item.title}</AlertTitle>
        {item.message}
      </Alert>
    )}
  </Div>

}

export default ViewAnnouncements