import React from 'react'
// import Form from 'Components/Styled/FormRegular'
import FormRegular from 'Components/Styled/FormRegular'
import { TextField } from '@material-ui/core'
import Input from 'Components/FormComps/InputReactHook'
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from '@hookform/resolvers';
import Button from 'Components/LoadingButton'
import * as yup from 'yup'
import {makeStyles} from '@material-ui/core/styles'
import {DateTimePicker} from '@material-ui/pickers'
import { snackbarInfo, snackbarSuccess } from 'Store/actions/snackbar';
import CommitRelay from 'Components/CommitRelay'
import graphql from 'babel-plugin-relay/macro';
import Select from 'Layout/FormBuilder/Select'

const addAnnouncement = graphql`
  mutation AnnouncementMutation($data: AnnouncementInput!){
    addAnnouncement(data: $data) {
      message
    }
  }
`

const schema = yup.object().shape({
  title: yup.string().required().max(20),
  message: yup.string().required().max(200),
  validTill: yup.string().required(),
  severity: yup.string().required()
});

function Announcement(props) {
  const [request, setRequest] = React.useState(false)
  const { register, handleSubmit, errors, control } = useForm({
    resolver: yupResolver(schema)
  });

  function onSubmit(data) {
    if(data.validTill === undefined){
      return snackbarInfo("Enter date till when announcement is valid")
    }
    data.validTill = new Date(data.validTill).getTime().toString()
    // console.log(data)
    setRequest(true)
    CommitRelay({data}, addAnnouncement, (response, error) => {
      setRequest(false)
      if(!error) {
        snackbarSuccess(response.addAnnouncement.message)
      }
    })
  }
  // console.log(errors)

  return <FormRegular onSubmit={handleSubmit(onSubmit)} component="form">
    <Input inputRef={register} label="Title" errors={errors} required={true} name="title"/>
    <Input inputRef={register} label="Message" errors={errors} required={true} multiline rows={5} name="message"/>
    <Controller 
      as={<DateTimePicker name="validTill" fullWidth label="Valid Till" disablePast/>}
      control={control} name="validTill"
    />
    <Controller 
      as={<Select options={["warning", "info", "error", "success"]} label="Type" name="severity"/>}
      control={control} name="severity"
    />
    <Button type="submit" isLoading={request}>Add Announcement</Button>
  </FormRegular>
}

export default Announcement