import React from 'react'
import Modal from 'Components/Modal'
import { Button } from '@material-ui/core'
import TablePagination from '@material-ui/core/TablePagination'
import QueryRenderer from 'Components/QueryRenderer'
import RegisteredUsersRefetch from './RegisteredUsersRefetch'
import graphql from 'babel-plugin-relay/macro'

const query= graphql`
  query RegisteredUsersQuery ($id: ID!){
    ...RegisteredUsersRefetch_users @arguments(id: $id, page: 1, limit: 4)
  }
`

function RegisteredUsers(props) {
  let [modalOpen, setModalOpen] = React.useState(false)
  return <div>
    <Button variant="contained" color="secondary" fullWidth onClick={() => setModalOpen(true)}> View Registered </Button>
    <Modal open={modalOpen} fullScreen handleClose={() => setModalOpen(false)} title={"Participated"} hideSubmit={true}>
      <QueryRenderer query={query} variables={{id: props.id}} component={data => <RegisteredUsersRefetch id={props.id} users={data} />} />
    </Modal>
  </div>
}

export default RegisteredUsers